
"use client";

import { useState, useEffect } from 'react';
import { DashboardHeader } from "@/components/DashboardHeader";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Search, Wrench, Bolt, Car, MapPin, Clock, CheckCircle } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { format } from "date-fns";
import { redirect } from "next/navigation";
import { Skeleton } from '@/components/ui/skeleton';

// Mock data fetching function. In a real app, you would fetch this from Firebase.
async function getUserData(userId: string | null) {
    if (!userId) return null;

    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000));

    // In a real app, you'd use the Firebase SDK to get data:
    // import { doc, getDoc } from "firebase/firestore";
    // import { db } from "@/lib/firebase";
    // const userDoc = await getDoc(doc(db, "users", userId));
    // if (!userDoc.exists()) return null;
    // return userDoc.data();

    // Returning mock data for now
    return {
        fullName: 'Jane Doe',
        activeBookings: [
            { id: '1', serviceType: 'Plumbing', worker: { fullName: 'John Smith' }, scheduledAt: new Date(), status: 'CONFIRMED' },
            { id: '2', serviceType: 'Electrical', worker: { fullName: 'Adam Jones' }, scheduledAt: new Date(Date.now() + 2 * 24 * 60 * 60 * 1000), status: 'PENDING' },
        ],
        pastBookings: [
            { id: '3', serviceType: 'Mechanical', worker: { fullName: 'Mike Miller' }, completedAt: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000), status: 'COMPLETED' },
            { id: '4', serviceType: 'Plumbing', worker: { fullName: 'John Smith' }, completedAt: new Date(Date.now() - 10 * 24 * 60 * 60 * 1000), status: 'CANCELED' },
        ]
    };
}


export default function UserDashboard() {
  // In a real app, you'd get the user from Firebase Auth
  const [userId, setUserId] = useState<string | null>('mock-user-id'); // 'mock-user-id' for demonstration
  const [userData, setUserData] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // In a real app, you'd use Firebase Auth to get the current user.
    // import { auth } from '@/lib/firebase';
    // const unsubscribe = auth.onAuthStateChanged(user => {
    //   if (user) {
    //     setUserId(user.uid);
    //   } else {
    //     redirect('/');
    //   }
    // });
    // return () => unsubscribe();
    
    // For now, we use a mock user ID
     if (!userId) {
        redirect('/');
     }

  }, [userId]);


  useEffect(() => {
    if (userId) {
        getUserData(userId).then(data => {
            if (!data) {
                redirect('/'); // Or show a 'not found' message
            } else {
                setUserData(data);
            }
            setLoading(false);
        });
    }
  }, [userId]);


  if (loading) {
    return (
        <div className="flex min-h-screen w-full flex-col">
            <DashboardHeader userName="Loading..." userRole="User" />
            <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
                <Skeleton className="h-12 w-1/2" />
                <Card><CardHeader><Skeleton className="h-8 w-1/4" /></CardHeader><CardContent><Skeleton className="h-32 w-full" /></CardContent></Card>
                <Card><CardHeader><Skeleton className="h-8 w-1/4" /></CardHeader><CardContent><Skeleton className="h-24 w-full" /></CardContent></Card>
                <Card><CardHeader><Skeleton className="h-8 w-1/4" /></CardHeader><CardContent><Skeleton className="h-24 w-full" /></CardContent></Card>
            </main>
        </div>
    )
  }

  const { fullName: userName, activeBookings, pastBookings } = userData;

  return (
    <div className="flex min-h-screen w-full flex-col">
      <DashboardHeader userName={userName} userRole="User" />
      <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
        <div className="flex items-center justify-between">
            <h1 className="font-headline text-2xl md:text-3xl font-bold tracking-tight">Welcome, {userName.split(' ')[0]}!</h1>
            <div className="flex items-center gap-2">
                <MapPin className="h-5 w-5 text-muted-foreground" />
                <span className="font-medium">New York, NY</span>
                <Button variant="outline" size="sm">Change</Button>
            </div>
        </div>
        
        <Card>
            <CardHeader>
                <CardTitle>Find a Service</CardTitle>
                <CardDescription>What do you need help with today?</CardDescription>
            </CardHeader>
            <CardContent>
                <div className="relative">
                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
                    <Input placeholder="Search for plumbers, electricians, mechanics..." className="pl-10 h-12 text-lg" />
                </div>
                <div className="mt-6 grid grid-cols-1 sm:grid-cols-3 gap-4">
                    <Button variant="secondary" className="h-16 text-base justify-start p-4">
                        <Wrench className="h-6 w-6 mr-4" /> Plumbing
                    </Button>
                    <Button variant="secondary" className="h-16 text-base justify-start p-4">
                        <Bolt className="h-6 w-6 mr-4" /> Electrical
                    </Button>
                    <Button variant="secondary" className="h-16 text-base justify-start p-4">
                        <Car className="h-6 w-6 mr-4" /> Mechanical
                    </Button>
                </div>
            </CardContent>
        </Card>

        <Card>
            <CardHeader>
                <CardTitle>Active Bookings</CardTitle>
                 {activeBookings.length === 0 && <CardDescription>You have no active bookings.</CardDescription>}
            </CardHeader>
             {activeBookings.length > 0 ? (
                <CardContent className="space-y-4">
                    {activeBookings.map((booking: any) => (
                        <div key={booking.id} className="flex items-center justify-between p-4 rounded-lg border">
                            <div>
                                <p className="font-bold">{booking.serviceType}</p>
                                <p className="text-sm text-muted-foreground">with {booking.worker.fullName}</p>
                                <p className="text-sm text-muted-foreground">
                                    <Clock className="inline h-4 w-4 mr-1" />
                                    {format(new Date(booking.scheduledAt), "EEE, MMM d, yyyy 'at' h:mm a")}
                                </p>
                            </div>
                            <Badge variant={booking.status === 'PENDING' ? 'secondary' : 'default'}>{booking.status}</Badge>
                        </div>
                    ))}
                </CardContent>
            ) : (
                <CardContent className="text-center text-muted-foreground">
                    <p>Your scheduled services will appear here.</p>
                </CardContent>
            )}
        </Card>
        <Card>
            <CardHeader>
                <CardTitle>Past Bookings</CardTitle>
                {pastBookings.length === 0 && <CardDescription>You have no past bookings.</CardDescription>}
            </CardHeader>
            {pastBookings.length > 0 ? (
                 <CardContent className="space-y-4">
                    {pastBookings.map((booking: any) => (
                        <div key={booking.id} className="flex items-center justify-between p-4 rounded-lg border">
                            <div>
                                <p className="font-bold">{booking.serviceType}</p>
                                 <p className="text-sm text-muted-foreground">with {booking.worker.fullName}</p>
                                <p className="text-sm text-muted-foreground">
                                    <CheckCircle className="inline h-4 w-4 mr-1" />
                                    {booking.completedAt ? format(new Date(booking.completedAt), "EEE, MMM d, yyyy") : 'N/A'}
                                </p>
                            </div>
                            <Badge variant={booking.status === 'CANCELED' ? 'destructive' : 'outline'}>{booking.status}</Badge>
                        </div>
                    ))}
                </CardContent>
            ) : (
                <CardContent className="text-center text-muted-foreground">
                    <p>Completed services will be logged here for your reference.</p>
                </CardContent>
            )}
        </Card>
      </main>
    </div>
  );
}
